<template>
    <v-card>
        <v-toolbar dark color="primary">
            <v-btn icon dark @click="close">
                <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>Search Image Food</v-toolbar-title>
        </v-toolbar>
        <v-divider></v-divider>
        <v-container fluid fill-height>
            <v-row align="center" justify="center">
                <v-col>
                    <v-card :key="componentKey">
                        <v-card-title class="text-h5">Take a Picture</v-card-title>
                        <v-card-text class="text-center">
                            <v-img v-if="capturedImage" :src="capturedImage" max-height="800" contain></v-img>
                            <video ref="video" v-else width="100%" height="auto" autoplay></video>
                            <canvas ref="canvas" style="display: none;"></canvas> <br>
                            <h3>Prediction Result: {{ predictionResult }}</h3>
                        </v-card-text>
                        <v-bottom-navigation v-model="value" :background-color="color" dark shift>
                            <v-btn @click="openGallery">
                                <span>Open</span>
                                <v-icon>image</v-icon>
                            </v-btn>

                            <v-btn @click="takePicture" :disabled="!cameraAccess">
                                <span>Shoot</span>
                                <v-icon>photo_camera</v-icon>
                            </v-btn>

                            <v-btn @click="previewPicture" :disabled="!capturedImage">
                                <span>Preview</span>
                                <v-icon>preview</v-icon>
                            </v-btn>
                            <input ref="fileInput" type="file" accept="image/*" @change="handleFileChange"
                                style="display: none" />
                        </v-bottom-navigation>
                    </v-card>
                    <v-dialog v-model="previewDialog" max-width="600">
                        <v-img :src="capturedImage" max-height="400" contain></v-img>
                        <v-card-actions>
                            <v-btn @click="closePreview" color="primary" icon>
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </v-card-actions>
                    </v-dialog>
                </v-col>
            </v-row>
        </v-container>
    </v-card>
</template>

<script>
import { mapActions } from 'vuex'
import axios from 'axios'
export default {
    name: 'camera',
    data() {
        return {
            cameraAccess: false,
            capturedImage: null,
            previewDialog: false,
            componentKey: 0,
            value: 1,
            predictionResult: null
        };
    },
    computed: {
        color() {
            switch (this.value) {
                case 0: return 'blue-grey'
                case 1: return 'purple'
                case 2: return 'brown'
                default: return 'blue-grey'
            }
        },
    },
    mounted() {
        this.initCamera();
    },
    methods: {
        ...mapActions({
            setStatusDialog: 'dialog/setStatus',
        }),
        close() {
            this.releaseCamera();
            this.setStatusDialog(false);
            window.location.reload(true);
        },
        forceRerender() {
            this.componentKey += 1;
        },
        async initCamera() {
            console.log('Init Camera Called');
            try {
                const stream = await navigator.mediaDevices.getUserMedia({ video: true });
                this.$refs.video.srcObject = stream;
                this.cameraAccess = true;
            } catch (error) {
                console.error('Error accessing camera:', error);
            }
        },
        async releaseCamera() {
            console.log('Release Camera Called');
            if (this.$refs.video.srcObject) {
                const tracks = this.$refs.video.srcObject.getTracks();

                tracks.forEach(track => {
                    track.stop();
                });

                this.$refs.video.srcObject = null;
            }
        },
        openGallery() {
            // Trigger the file input programmatically
            this.$refs.fileInput.click();
        },

        handleFileChange(event) {
            const file = event.target.files[0];
            if (file) {
                const reader = new FileReader();

                reader.onload = () => {
                    this.capturedImage = reader.result;
                };

                reader.readAsDataURL(file);

                const formData = new FormData();
                formData.append('file', file, 'image.jpg');

                axios.post('https://ml-nutriku.techside.my.id/predict', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                })
                    .then(response => {
                        console.log(response.data);
                        this.predictionResult = response.data.class;
                    })
                    .catch(error => {
                        console.error(error);
                    });
            }
        },
        takePicture() {
            if (this.capturedImage) {
                this.initCamera();
                this.capturedImage = null;
            } else {
                const video = this.$refs.video;
                const canvas = this.$refs.canvas;
                const context = canvas.getContext('2d');

                canvas.width = video.videoWidth;
                canvas.height = video.videoHeight;

                context.drawImage(video, 0, 0, canvas.width, canvas.height);
                this.capturedImage = canvas.toDataURL('image/png');
                this.releaseCamera();
                canvas.toBlob((blob) => {
                    const formData = new FormData();
                    formData.append('file', blob, 'image.jpg');

                    axios.post('https://ml-nutriku.techside.my.id/predict', formData, {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    })
                        .then(response => {
                            console.log(response.data);
                            this.predictionResult = response.data.class;
                        })
                        .catch(error => {
                            console.error(error);
                        });

                }, 'image/jpeg');
            }
        },
        navigateToFood(predictionResult) {
            const newRoute = `/food/${predictionResult}`;

            if (this.$route.path !== newRoute) {
                this.$router.push(newRoute);
            }
        },
        previewPicture() {
            this.navigateToFood(this.predictionResult);
            this.setStatusDialog(false);
        },
        closePreview() {
            this.previewDialog = false;
            this.capturedImage = null;
            this.initCamera();
        },
    }
}
</script>

<style>
.text-center {
    display: block;
}

video {
    border-radius: 10px;
}
</style>